.footer_outer_container {
  background-color: #111;
  color: grey;
  display: flex;
  justify-content: center;
  padding: 30px 0;
  flex-direction: column;
  align-items: center;
}
.footer_inner_container {
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.footer_icons {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
}
.footer_data {
  display: flex;
  justify-content: space-between;
  width: 100%;
  flex-wrap: wrap;
  margin-bottom: 20px;
}
.footer_data ul {
  list-style: none;
  padding: 0;
  margin: 0 20px;
}
.footer_data ul li {
  cursor: pointer;
  margin: 8px 0;
  color: #999;
  font-size: 14px;
}
.service_code {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #fff;
  width: 120px;
  height: 30px;
  color: white;
  margin-bottom: 10px;
  cursor: pointer;
  font-size: 14px;
}
.copyright {
  font-size: 12px;
  color: grey;
}